/*blog card*/
.blogcard {
  position: relative;
  width: 95%;
  padding-top: 1.5em;
}
.even {
  margin-left: auto;
}
.odd {
  margin-right: auto;
}

.catcard {
  width: 100%;
}

.cardimg.cardimgselected {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.cardimg {
  display: block;
  height: 45vh;
  width: 100%;
  object-fit: cover;
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
}

.cardtitle {
  color: var(--primary);
}

.caption {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  font-weight: bold;
  color: var(--primary);
}

p.cardsummary {
  font-style: italic;
}

.blogcard:hover {
  cursor: pointer;
}

