.loadingContainer {
  position: relative;
  margin: 100px auto 0 auto;
  padding-top: 60px;
  width: 110px;
}
.loadingContainer div {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: black;
  float: left;
  margin: 0 3px;
  background: #6859a3;
}
.loadingContainer .ball1 {
  z-index: 1;
  animation: bounce 3s infinite ease-in-out;
  -moz-animation: bounce 3s infinite ease-in-out;
  -webkit-animation: bounce 3s infinite ease-in-out;
}
.loadingContainer .ball2 {
  animation: bounce 3s infinite ease-in-out;
  -moz-animation: bounce 3s infinite ease-in-out;
  -webkit-animation: bounce 3s infinite ease-in-out;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}
.loadingContainer .ball3 {
  animation: bounce 3s infinite ease-in-out;
  -moz-animation: bounce 3s infinite ease-in-out;
  -webkit-animation: bounce 3s infinite ease-in-out;
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}
.loadingContainer .ball4 {
  animation: bounce 3s infinite ease-in-out;
  -moz-animation: bounce 3s infinite ease-in-out;
  -webkit-animation: bounce 3s infinite ease-in-out;
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s;
}

@keyframes bounce {
  0%,
  15% {
    -moz-transform: translate(0, 0);
  }
  50% {
    -moz-transform: translate(0, -30px);
    background: #72c2c2;
  }
  85%,
  100% {
    -moz-transform: translate(0, 0);
  }
}
@-webkit-keyframes bounce {
  0%,
  20% {
    -webkit-transform: translate(0, 0);
  }
  50% {
    -webkit-transform: translate(0, -30px);
    background: #72c2c2;
  }
  80%,
  100% {
    -webkit-transform: translate(0, 0);
  }
}

/*mobile*/
.navbar {
  padding: 2em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid #f2f2f2;
}

.logo {
  text-decoration: none;
  font-size: 1.2em;
}

.navbar .navlinks {
  display: none;
}

.navicons {
  margin-left: auto;
}

.mobile-menu {
  padding-left: 1em;
  transition: 0.2s;
}

.mobile-menu:hover {
  cursor: pointer;
}

.mobile-menu.closebtn {
  padding: 1em;
}

.toggle-theme:hover {
  cursor: pointer;
}

.mobile-navlinks {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 2;
  top: 0;
  right: 0;
  background-color: var(--tertiary);
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.mobile-navlinks a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: var(--primary);
  display: block;
  transition: 0.4s;
}

.mobile-navlinks a:hover {
  background-color: var(--tertiary);
  color: var(--secondary);
}

/*large tablets*/
@media screen and (min-width: 960px) {
  .navbar .navlinks {
    display: block;
    margin-left: auto;
  }

  .navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
  }
  .navbar a:hover {
    color: #f1356d;
  }

  .mobile-menu {
    display: none;
  }

  ul.mobile-navlinks {
    display: none;
  }
}

/*create*/
div.create {
  text-align: center;
  padding: 2em 4em;
}
.create h2 {
  padding: 20px;
}

.create span {
  color: var(--secondary);
}

/*snackbar*/
#snackbar {
  visibility: hidden;
  margin-left: -125px; /* Divide value of min-width by 2 */
  background-color: var(--secondary); /* Black background color */
  color: var(--tertiary); /* White text color */
  text-align: center; /* Centered text */
  border-radius: 2px; /* Rounded borders */
  padding: 16px; /* Padding */
  position: fixed; /* Sit on top of the screen */
  z-index: 1; /* Add a z-index if needed */
  left: 50%; /* Center the snackbar */
  bottom: 30px; /* 30px from the bottom */
}
.selectedtags {
  border-color: var(--tertiary);
  border: 2px;
  border-style: solid;
  padding: 1em;
  margin: 2em 0em;
}

.newtag {
  display: -webkit-flex;
  display: flex;
}

.newtag a {
  text-align: center;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Animations to fade the snackbar in and out */
@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

/*input fields*/
input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid var(--tertiary);
  border-radius: 4px;
  box-sizing: border-box;
  color: var(--secondary);
  outline: none;
}

/*DraftJS Editor*/
.create div.DraftEditor-root {
  background-color: var(--tertiary);
  height: 300px;
  overflow-y: auto;
  font-size: 18px;
  font-family: "calibri", sans-serif;
  padding: 10px;
  padding-bottom: 20px;
  overflow-x: hidden;
}

div.DraftEditor-editorContainer,
div.public-DraftEditor-content {
  height: 100%;
}

/*toolbar*/
div.toolbar {
  position: relative;
  padding: 10px 20px;
  margin: auto;
  text-align: right;
  background-color: var(--secondary);
}
div.toolbar button {
  border: 1px solid var(--four);
  padding: 5px;
  margin-right: 4px;
  background-color: var(--four);
  color: var(--tertiary);
  cursor: pointer;
  transition-duration: 0.2s;
}

div.toolbar button:hover {
  background-color: var(--tertiary);
  color: var(--four);
}

button#createsubmit {
  color: var(--secondary);
  width: 100%;
  height: 50px;
  border: 0;
  transition-duration: 0.4s;
}

button#createsubmit:hover:enabled {
  background-color: var(--secondary);
  cursor: pointer;
  color: var(--tertiary);
}

.tagopts {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.tagopt {
  padding: 0.5em 1em;
  margin: 1em;
  border-radius: 0.5em;
}

.tagopt:hover {
  cursor: pointer;
}

.tagopt.selected {
  color: var(--primary);
  background-color: var(--secondary);
}

.about h2 {
  text-align: center;
  padding-top: 2em;
}

.about {
  padding: 0em 20em;
}

#slogan {
  font-style: italic;
}

#signature {
  text-align: end;
  font-style: italic;
}

article h2 {
  text-align: center;
  padding: 1em 0;
  color: var(--secondary);
  background-color: var(--tertiary);
}

div.basicinfo {
  background-color: var(--four);
  display: -webkit-flex;
  display: flex;
  text-align: left;
  height: auto;
  padding: 2em 9em;
  border-radius: 0 0 1em 1em;
}

.col {
  float: left;
  padding: 10px;
  line-height: 1.5em;
}

.left {
  width: 25%;
}
.right {
  width: 75%;
}

.blogbody {
  padding: 1em 1em 0 1em;
