  background-color: var(--secondary);
  font-weight: bold;
}
.deleteblogdiv {
  color: white;
}
.deleteblog {
  position: absolute;
  left: 95%;
  top: 25%;
}

.navigate {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 1em 0;
}

a:hover {
  cursor: pointer;
  color: var(--secondary);
}

.navigate a {
  transition: 0.3s;
}

.navigate a:hover {
