}

/*DraftJS Editor*/
.blog-details div.DraftEditor-root {
  background-color: var(--primary);
  font-size: 1em;
  padding: 2em 9em;
  padding-bottom: 20px;
  overflow-x: hidden;
  line-height: 1.6;
}

.commentbox {
  text-align: center;
  padding: 2em 9em;
}

.commentbox h3 {
  text-align: left;
  padding: 1em 0em;
}

.commentbox textarea {
  width: 100%;
  box-sizing: border-box;
  margin: 1em 0;
  padding: 1em;
  color: var(--secondary);
  resize: none;
  border: 1px solid var(--tertiary);
  outline: none;
}

.commentbtn {
  width: 100%;
  padding: 1em 0;
  background-color: var(--secondary);
  color: var(--primary);
  transition: 0.3s;
}

.commentbtn:hover {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.categories {
  padding: 1em 3em;
}

.bloggrid {
  max-width: 2000px;
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.cardimgsmall {
  height: 45vh;
  width: 100%;
  object-fit: cover;
  padding-top: 1em;
}

footer {
  text-align: center;
  vertical-align: middle;
  padding: 1em;
  margin-top: 2em;
  background-color: var(--secondary);
  color: var(--primary);
  clear: both;
  position: relative;
}

footer p {
  color: var(--primary);
}

footer svg {
  padding: 1em;
}

#insta {
  color: var(--primary);
}

.auth {
  text-align: center;
  margin: 50px auto;
  padding: 0 5em;
}

.auth input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  outline: none;
}

.auth button {
  width: 100%;
  margin-top: 0.5em;
  padding: 1em;
  border: none;
}

[type="submit"] {
  background-color: var(--secondary);
  color: var(--primary);
  opacity: 90%;
}

[type="submit"]:hover {
  cursor: pointer;
  opacity: 100%;
}

.switch-auth {
  background-color: var(--tertiary);
  opacity: 90%;
}

.switch-auth:hover {
  cursor: pointer;
  opacity: 100%;
}

/* variables */
:root {
  --primary: #ffffff;
  --secondary: #2f3542;
  --tertiary: #a4b0be;
}

.socials a {
  color: #ffffff;
  color: var(--primary);
}

/* base styles */
* {
  margin: 0;
}

h1,
h2,
h3,
h4 {
  font-family: "Alegreya Sans SC", sans-serif;
  color: #2f3542;
  color: var(--secondary);
}

:not(h1, h2, h3, h4, svg) {
  font-family: "Source Sans Pro", sans-serif;
}

article {
  color: #2f3542;
  color: var(--secondary);
}

a {
  color: #2f3542;
  color: var(--secondary);
}
body {
  background-color: #ffffff;
  background-color: var(--primary);
}

.content {
  min-height: calc(100vh - 123px - 157px);
}

