.commentbox {
  text-align: center;
  padding: 2em 9em;
}

.commentbox h3 {
  text-align: left;
  padding: 1em 0em;
}

.commentbox textarea {
  width: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 1em 0;
  padding: 1em;
  color: var(--secondary);
  resize: none;
  border: 1px solid var(--tertiary);
  outline: none;
}

.commentbtn {
  width: 100%;
  padding: 1em 0;
  background-color: var(--secondary);
  color: var(--primary);
  transition: 0.3s;
}

.commentbtn:hover {
  cursor: pointer;
}

a {
  text-decoration: none;
}
