/*create*/
div.create {
  text-align: center;
  padding: 2em 4em;
}
.create h2 {
  padding: 20px;
}

.create span {
  color: var(--secondary);
}

/*snackbar*/
#snackbar {
  visibility: hidden;
  margin-left: -125px; /* Divide value of min-width by 2 */
  background-color: var(--secondary); /* Black background color */
  color: var(--tertiary); /* White text color */
  text-align: center; /* Centered text */
  border-radius: 2px; /* Rounded borders */
  padding: 16px; /* Padding */
  position: fixed; /* Sit on top of the screen */
  z-index: 1; /* Add a z-index if needed */
  left: 50%; /* Center the snackbar */
  bottom: 30px; /* 30px from the bottom */
}
.selectedtags {
  border-color: var(--tertiary);
  border: 2px;
  border-style: solid;
  padding: 1em;
  margin: 2em 0em;
}

.newtag {
  display: flex;
}

.newtag a {
  text-align: center;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Animations to fade the snackbar in and out */
@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

/*input fields*/
input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid var(--tertiary);
  border-radius: 4px;
  box-sizing: border-box;
  color: var(--secondary);
  outline: none;
}

/*DraftJS Editor*/
.create div.DraftEditor-root {
  background-color: var(--tertiary);
  height: 300px;
  overflow-y: auto;
  font-size: 18px;
  font-family: "calibri", sans-serif;
  padding: 10px;
  padding-bottom: 20px;
  overflow-x: hidden;
}

div.DraftEditor-editorContainer,
div.public-DraftEditor-content {
  height: 100%;
}

/*toolbar*/
div.toolbar {
  position: relative;
  padding: 10px 20px;
  margin: auto;
  text-align: right;
  background-color: var(--secondary);
}
div.toolbar button {
  border: 1px solid var(--four);
  padding: 5px;
  margin-right: 4px;
  background-color: var(--four);
  color: var(--tertiary);
  cursor: pointer;
  transition-duration: 0.2s;
}

div.toolbar button:hover {
  background-color: var(--tertiary);
  color: var(--four);
}

button#createsubmit {
  color: var(--secondary);
  width: 100%;
  height: 50px;
  border: 0;
  transition-duration: 0.4s;
}

button#createsubmit:hover:enabled {
  background-color: var(--secondary);
  cursor: pointer;
  color: var(--tertiary);
}

.tagopts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.tagopt {
  padding: 0.5em 1em;
  margin: 1em;
  border-radius: 0.5em;
}

.tagopt:hover {
  cursor: pointer;
}

.tagopt.selected {
  color: var(--primary);
  background-color: var(--secondary);
}
