.auth {
  text-align: center;
  margin: 50px auto;
  padding: 0 5em;
}

.auth input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  outline: none;
}

.auth button {
  width: 100%;
  margin-top: 0.5em;
  padding: 1em;
  border: none;
}

[type="submit"] {
  background-color: var(--secondary);
  color: var(--primary);
  opacity: 90%;
}

[type="submit"]:hover {
  cursor: pointer;
  opacity: 100%;
}

.switch-auth {
  background-color: var(--tertiary);
  opacity: 90%;
}

.switch-auth:hover {
  cursor: pointer;
  opacity: 100%;
}
