article h2 {
  text-align: center;
  padding: 1em 0;
  color: var(--secondary);
  background-color: var(--tertiary);
}

div.basicinfo {
  background-color: var(--four);
  display: flex;
  text-align: left;
  height: auto;
  padding: 2em 9em;
  border-radius: 0 0 1em 1em;
}

.col {
  float: left;
  padding: 10px;
  line-height: 1.5em;
}

.left {
  width: 25%;
}
.right {
  width: 75%;
}

.blogbody {
  padding: 1em 1em 0 1em;
  line-height: 1.5em;
}

.detailsimg {
  width: 100%;
  height: 70vh;
  object-fit: cover;
  display: block;
  filter: brightness(0.9);
  animation: fadeIn 1s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.blogtitle {
  position: absolute;
  left: 50%;
  top: 55%;
  padding: 1em;
  transform: translate(-50%, -50%);
  text-align: center;
  color: var(--tertiary);
  background-color: var(--secondary);
  font-weight: bold;
}
.deleteblogdiv {
  color: white;
}
.deleteblog {
  position: absolute;
  left: 95%;
  top: 25%;
}

.navigate {
  display: flex;
  justify-content: space-between;
  padding: 1em 0;
}

a:hover {
  cursor: pointer;
  color: var(--secondary);
}

.navigate a {
  transition: 0.3s;
}

.navigate a:hover {
  font-size: 20px;
}

/*DraftJS Editor*/
.blog-details div.DraftEditor-root {
  background-color: var(--primary);
  font-size: 1em;
  padding: 2em 9em;
  padding-bottom: 20px;
  overflow-x: hidden;
  line-height: 1.6;
}
