@import url("https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:wght@300&family=Source+Sans+Pro:wght@300&display=swap");

/* variables */
:root {
  --primary: #ffffff;
  --secondary: #2f3542;
  --tertiary: #a4b0be;
}

.socials a {
  color: var(--primary);
}

/* base styles */
* {
  margin: 0;
}

h1,
h2,
h3,
h4 {
  font-family: "Alegreya Sans SC", sans-serif;
  color: var(--secondary);
}

:not(h1, h2, h3, h4, svg) {
  font-family: "Source Sans Pro", sans-serif;
}

article {
  color: var(--secondary);
}

a {
  color: var(--secondary);
}
body {
  background-color: var(--primary);
}

.content {
  min-height: calc(100vh - 123px - 157px);
}

