/*mobile*/
.navbar {
  padding: 2em;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f2f2f2;
}

.logo {
  text-decoration: none;
  font-size: 1.2em;
}

.navbar .navlinks {
  display: none;
}

.navicons {
  margin-left: auto;
}

.mobile-menu {
  padding-left: 1em;
  transition: 0.2s;
}

.mobile-menu:hover {
  cursor: pointer;
}

.mobile-menu.closebtn {
  padding: 1em;
}

.toggle-theme:hover {
  cursor: pointer;
}

.mobile-navlinks {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 2;
  top: 0;
  right: 0;
  background-color: var(--tertiary);
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.mobile-navlinks a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: var(--primary);
  display: block;
  transition: 0.4s;
}

.mobile-navlinks a:hover {
  background-color: var(--tertiary);
  color: var(--secondary);
}

/*large tablets*/
@media screen and (min-width: 960px) {
  .navbar .navlinks {
    display: block;
    margin-left: auto;
  }

  .navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
  }
  .navbar a:hover {
    color: #f1356d;
  }

  .mobile-menu {
    display: none;
  }

  ul.mobile-navlinks {
    display: none;
  }
}
